import react, { useContext, useReducer, useState } from "react";
import { useGetInitialData } from "./useGetInitialData";
const allGenresContext = react.createContext()
const trendingsListContext = react.createContext()
const genresMoviesContext = react.createContext()

export const useAllGenresContext = () => {
    return useContext(allGenresContext);
}
export const useTrendingsListContext = () => {
    return useContext(trendingsListContext)
}
export const useGenresMoviesContext = () => {
    return useContext(genresMoviesContext);
}

export default function AllGenresMoviesProvider({ children }) {
    const [trendingList, setTrendingList] = useState({
    loading: true,
    data: undefined,
    error: null
  });

  const [genresList, setGenresList] = useState({
    loading: true,
    data: undefined,
    error: null
  });
  
  // const [genresMovies, setGenresMovies] = useState( {} );
  const reducer =  (genresMovies, action)=> {
    
    if ( genresMovies[ action.data.name ] ) {
      // console.log('genres movies inside reducer  returing same as befor')
      // console.log(genresMovies)
      return genresMovies
    } else {
      let temp = genresMovies;
      temp[action.name] = action.data ;
      // console.log('genres movies inside reducer returning new one with genre bellow added to it')
      // console.log(action.name)
      // console.log(temp)
      return temp
    }
    
  }
  const [genresMovies, setGenresMovies] = useReducer( reducer , {} );
  useGetInitialData(setTrendingList , setGenresList)

  let temp={};
  if ( !genresList.loading && !genresList.error && genresList.data ) {
    genresList.data.genres.forEach(genre => {
      temp[genre.name] = [ ]
    });
  }
  // const genresMoviesReducer = (state, action) => {
  //   /*
  //     action should be an object with these methodes:
  //     type: eithere check for check and get needed values or update to update current state
  //       check =>
  //         genreName : name of the genre you are looking for or want to update it
  //         genreId :  id of the genre you are lookning for
  //         setGenreMovies:  a setter function to update state 
  //       update => 
  //         genreMovies:  contain movies data u want to add to state
  //         genreName : name of the genre u want to update data 

  //   */
  // switch (action.type) {
  //   case 'check':
  //     if ( !state[action.genreName] ) {
  //       <GetGenreMovies 
  //       setGenreMovies={action.setGenreMovies} 
  //       genreId= {action.genreId} 
  //       />
  //     } else {
  //       action.setGenreMovies(state[action.genreName])
  //     }
  //     return state
  //   case 'update':
  //     if ( !state[action.genreName] ) {
  //       let temp = state
  //       temp[action.genreName] = action.genreMovies
  //       return temp
  //     } else {
  //       return state
  //     }
      
  //   default:
  //     break;
  // }
        
  // }

  return (
    <allGenresContext.Provider value={ { genresList, setGenresList }}>
        <trendingsListContext.Provider value={{ trendingList,setTrendingList }}>
          <genresMoviesContext.Provider value={{ genresMovies, setGenresMovies }}>
              { children }
          </genresMoviesContext.Provider>
        </trendingsListContext.Provider>
    </allGenresContext.Provider>
  )
  ;
}
