import { useEffect } from "react";
import { generateUrl } from "../generateUrl";
import axios from "axios";
export const useGetInitialData = (setTrendingList , setGenresList) => {
    // const { setTrendingList } = useTrendingsListContext();
    // const { setGenresList } =  useAllGenresMoviesContext()
    useEffect(() => {
      // this part is for fetching data for top banner
      const trendingListApi = generateUrl('weeklyTrend')
  
      axios.get(trendingListApi.url).then(response => {
        setTrendingList({
          loading: false,
          data: response.data,
          error: false
        })
      }).catch(error =>{
        setTrendingList({
          loading: false,
          data: 'somthing went rong if you are from iran pleas turn on your vpn',
          error: true
        })
      });
      // this part is for fetching data for genres to feed on each row slider
      const genresApi = generateUrl('genresList')
      axios.get(genresApi.url).then(response => {
        setGenresList({
          loading: false,
          data: response.data,
          error: false
        })
      }).catch(error =>{
        console.log(error);
        setGenresList({
          loading: false,
          data: 'somthing went rong if you are from iran pleas turn on your vpn',
          error: true
        })
      });
      return () => {
      };
    }, [setTrendingList , setGenresList]);
  }