import React, { useEffect, useRef, useState } from 'react';
import api from '../api';
import { useGetTrailer } from '../customHooks/useGetTrailer';
import '../style/infoPopUp.css'
function InfoPopUp(movie) {
    const [trailerBtn, setTrailerBtn] = useState('');
    let trailer = useGetTrailer(movie.movie.id);
    // const [trailerLinke, setTrailerLinket] = useState('');
    useEffect(() => {
        if (trailer?.loading === false && trailer?.error === false && trailer?.linke) {
           setTrailerBtn( 
            <a className ='trailer-btn' rel="noreferrer" target="_blank" href={trailer.linke}>whatch trailer</a>)
        }
      
        return () => {
      };
    }, [trailer]);
    
    // setTrailerLinket( useGetTrailer(movie.movie.id));
    const content =  useRef( '' );
    // const [trailerBtn, setTrailerBtn] = useState();
    content.current = (
        <div className='info-popup'>
        <div className='info-card'>
            <img alt={movie.movie.title} src={`${api.imageBaseUrl}/w200${movie.movie.poster_path}`}/>
            <h4>{movie.movie.original_title}</h4>
            <p>{movie.movie.release_date}</p>
            {trailerBtn || ''}
        </div>
        <div className='info-overview'>
            <p>{movie.movie.overview}</p>
        </div>
    </div>
    )
    
    
    
  return content.current; 
}

export default InfoPopUp;
