import  { useEffect, useState } from "react";
import { generateUrl } from "../generateUrl";
import axios from "axios";
const useGetGenereMovies = (genreId,page) => {
    const [movies, setMovies] = useState({
        loading: true,
        data: undefined,
        error: false
      });

    useEffect(() => {
        const moviesApi = generateUrl('genreMovies',{ id : genreId, page : page })
        axios.get(moviesApi.url, {params:moviesApi.params}).then(response => {
            setMovies({
            loading: false,
            data: response.data,
            error: false
          })
        }).catch(error =>{
            setMovies({
            loading: false,
            data: 'somthing went rong if you are from iran pleas turn on your vpn',
            error: true
          })
        });
        return () => {
        };
      }, [genreId, page]);
      return movies

}
export default useGetGenereMovies