
import React, { useEffect, useState} from 'react';
import api from '../api';
import { useGetTrailer } from '../customHooks/useGetTrailer';

function Banner({movie}) {
  
  let trailerLinke
  const [content, setContent] = useState();
  trailerLinke = useGetTrailer(movie.id).linke

  useEffect(() => {
    setContent(
        <>
          <div className='banner-info'>
            <div className='title-container'>
              <h3 className='title'>{movie.title} <span className='banner-vote'>{movie.vote_average}</span></h3>
              <a className='trailer-btn' rel="noreferrer" target="_blank" href={trailerLinke}>whatch trailer</a>
            </div>
            <div className='overview-container'>
              <p className='overview'>{movie.overview}</p>
            </div>
          </div>
          <img src={`${api.imageBaseUrl}/original${movie.backdrop_path}`} alt='turn on ur vpn'></img>
        </>
        )
    
      return () => {
      };
    }, [trailerLinke, movie]);
    
  
    
    
  return (
      <div className='banner'>
        { content || <p>loading...</p> }
      </div>
  );
}

export default Banner;
