import { useEffect } from 'react';
import useGetGenereMovies from '../customHooks/useGetGenreMovies';

function GetGenreMovies({setReRender, setGenresMovies, genreId , page, name}) {

  let response = useGetGenereMovies( genreId,page )
  useEffect(() => {
    
    if ( !response.loading ) {
      setGenresMovies({
        name: name,
        data: response.data
      })
      setReRender(true)
      // console.log('rerender set to true')
      // console.log('renderless comp done');
    }
    return () => {
    };
  }, [response, name, setReRender, setGenresMovies]);
  
  return null;
}

export default GetGenreMovies;
