import React, { useState } from 'react';
import Slider from 'react-slick'  ;
import { useEffect } from 'react';
import MovieCard from './MovieCard';
import Banner from './Banner';
import { shuffle } from '../shuffleArray';
import InfoPopUp from './InfoPopUp';
import { useTrendingsListContext } from '../customHooks/InfoContext';
import Loader from './loader';
import { useGenresMoviesContext } from '../customHooks/InfoContext';
import GetGenreMovies from './GetGenreMovies';
// -------------------------------------------------------------------------------------
export  function BannerSlider() {
  let { trendingList }  = useTrendingsListContext()
  let moviesBanners;
  let shuffledTrendingList;
  if(trendingList !== undefined){
    shuffledTrendingList = shuffle(trendingList.data.results)
    moviesBanners = (shuffledTrendingList.map(movie =>{
      return( <Banner key={movie.id} movie={movie}/> )
    }))
  }

  return (
    <>
      <Slider className='row' { ...{
        infinite: true,
        speed: 500,
        autoplay: true,
        autoplaySpeed: 5000,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        dots: false,
    }}>
        {moviesBanners}
    </Slider>
  </>
    );
}

//---------------------------------------------------------------------------

const rowSlider = (movies) => {
  return (<Slider className='movie-slider-row' { ...{
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 4,
    arrows: true,
    dots: false,
    rtl : true,
    responsive: [
        {
          breakpoint: 1700,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 3
          }
        },
        {
          breakpoint: 1580,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 2
          }
        },
        {
          breakpoint: 1150,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            arrows: false,
          }
        },
        {
          breakpoint: 700,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
          }
        }
      ]
}}>
    {movies}
</Slider>)
}
export function MovieCardSlider({id, name}) {

  const [popUp, setPopUp] = useState('');
  const [popupMovie, setPopupMovie] = useState(null);
  useEffect(() => {
    if(popupMovie){
      setPopUp(<InfoPopUp state movie={popupMovie} />)
    } else {
      setPopUp('')
    }

    return () => {
    };
  }, [popupMovie]);

  

  const { genresMovies, setGenresMovies } = useGenresMoviesContext()
  // console.log('genresmovies context ')
  // console.log(genresMovies)
  const [getData, setGetData] = useState( false );
  const [reRender, setReRender] = useState(false)
  if ( !genresMovies[name] && !getData ) {
    // console.log('data was not available so setting get data to true for genre ')
    // console.log(name)
    setGetData(true)
  } 
  
  if(genresMovies[name] && getData) {
    // console.log('data was available and get data was true so setting it to false')
    // console.log('---------------------------------------------')
    // console.log(name)
    // console.log(genresMovies[name])
    setGetData(false)
    
  }

  const [moviesCardsSlider, setMoviesCardsSlider] = useState(<Loader />)
  // let moviesCardsSlider = useRef( <Loader /> )
  useEffect(() => {
    
    if( genresMovies[name] ){
      let slider = rowSlider( genresMovies[name].results.map(movie => {
        // console.log('movie to movie card')
        // console.log(movie)
        let temp = ''
        if (movie.backdrop_path ) {
          return(
          <MovieCard
            key={movie.id}
            popupMovie={popupMovie}
            setPopupMovie={setPopupMovie}
            movieInfo={ movie
            }/>
            )
        }
        return temp;
      }) )
      setMoviesCardsSlider( slider )
        
      // console.log('moviesCards.current')
      // console.log(moviesCards.current)
  }

    return () => {
    };
  }, [popupMovie, genresMovies, name, reRender]);

    return (
        <div className='row-container'>
        {getData ? <GetGenreMovies genreId={id} setGenresMovies={setGenresMovies} page={1} name={name} setReRender={ setReRender }/> : ''}
            <h3 className='row-title'>{name}</h3>
            { moviesCardsSlider }
            {popUp}
        </div>
    )
}


