import React from 'react';
import {BannerSlider, MovieCardSlider} from '../components/slider';
import Loader from '../components/loader';
import { useAllGenresContext, useTrendingsListContext } from '../customHooks/InfoContext';
function MainPage() {
  const { genresList } = useAllGenresContext()
  let rows;
  if( genresList.data !== undefined && genresList.error === false ){
    rows = genresList.data.genres.map(genre => { return(
    <MovieCardSlider key={genre.id} id={genre.id} name = {genre.name} />
    ) })
  } else if( genresList.error === false ){
    rows = <Loader />
  }
  const {trendingList} = useTrendingsListContext()
  let banner;
  if(trendingList.data !== undefined && trendingList.error === false){
    banner = <BannerSlider/>
  } else if(trendingList.error === false){

    banner = <Loader/> 

  } else {
    banner = <h1 className='api-connection-err'>an error acurred if you are connecting from iran pleas turn on your vpn</h1>
  }
  return (
    <>
      <div className='banner-container'>
        { banner }
      </div>
      <div className='rows-container'>
        {rows}
      </div>
      <div className='footer'></div>
    </>
      )
}

export default MainPage;
