import axios from "axios";
import { useEffect, useState } from "react";
import { generateUrl } from "../generateUrl";

export const useGetTrailer = (movieId) => {
  
  const [trailerLinke, setTrailerLinke] = useState({
    loading:true,
    linke : undefined,
    error: false
    });  
  const [response, setReqsponse] = useState({
    loading:true,
    data : undefined,
    error: false
    });
  useEffect(() => {
    let request = generateUrl('videos', {id : movieId});
    axios.get( request.url, { params:request.params }).then(response => {
    if (response.status === 200 && response.data) {
      setReqsponse({
        loading: false,
        data:  response.data,
        error: false
      })  
    }

  }).catch( error => {
    setReqsponse({
      loading: false,
      data: undefined,
      error: [true, error]
    })
  })

    return () => {
    };
  }, [ movieId ]);
  
  useEffect(() => {
    if( !response.loading && !response.error && response.data){
      try {
        let i =0;
        while(true){
          if(response.data.results[i].type === 'Trailer' && response.data.results[i].site === 'YouTube'){
            setTrailerLinke( {
              loading:false,
              linke : `https://www.youtube.com/watch?v=${response.data.results[i].key}`,
              error: false
              } )
            break;
          }
          i++;
        }
        
      } catch (error) {
        console.log(error);
        try {
          setTrailerLinke( {
            loading:false,
            linke : `https://www.youtube.com/watch?v=${response.data.results[1].key}`,
            error: false
          } )
          
        } catch (error) {
          // console.log('no video for this movie');
          setTrailerLinke({
            loading:true,
            linke : undefined,
            error: false
            })
        }
      
    }
            }
    
  
    return () => {
      
    };
  }, [ response ]);
      return trailerLinke;
 }