import api from "./api";
export const generateUrl = (requestType, args) =>{
    switch (requestType) {
        case 'genresList':
          return( {url : `${ api.BASE_URL }${ api.genresList }` , params : {}});
        case "genreMovies":
          return( {url : `${api.BASE_URL}${api.genreMovies}`, params : {with_genres : args.id, page : args.page}});
        case "weeklyTrend":
          return( {url : `${api.BASE_URL}${api.weeklyMovieTrendingList}` , params:{}});
          case "videos":
            return( {url : `${api.BASE_URL}/movie/${args.id}/videos` , params:{api_key:api.API_KEY}});
        default:
            return {url:'',params:{}};
    };
}