import React  from 'react';
import api from '../api';
import '../style/card.css'
function MovieCard({popupMovie, setPopupMovie, movieInfo}) {

  const backDropUrl = `${api.imageBaseUrl}/w300${movieInfo.backdrop_path}`;
  const onclick = () => {
    if(movieInfo.title !== popupMovie?.title){
      setPopupMovie(movieInfo);
    }else {
      setPopupMovie(null);
    }
  }
  return (
    <div className='movie-card' onClick={onclick} key={movieInfo.id}>
        <img src={backDropUrl} alt={movieInfo.title}></img>
        <div className='info-container'>
          <h5 className='movie-title'>{movieInfo.title}  </h5>
          <h6 className='vote'> vote : {movieInfo.vote_average}</h6>
        </div>


    </div>
  );
}

export default MovieCard;
